import React, { Component, useEffect, useState } from 'react';
import moment from 'moment';
import { useHistory, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import globalStyles from '../../common/style';
import Medium from "../../assets/png/medium.png";
import { toggleSendMessageModal } from '../../modules/static/modals';
import PageLoader from '@components/Loader/index';
import { getCategoryDetails, getCategoryArticles } from '@modules/cms';
import '@components/CategoryArticles/index.scss';
import { ReactMarkdown } from 'react-markdown/lib/react-markdown';
import DocumentSettings from '@utils/seo';
import PostSocials from '@components/PostSocials';
import SingleCourse from '@components/SingleCourse';

const CategoryArticles: React.FunctionComponent<any> = (props) => {
    const history = useHistory();
    const [loaded, setLoaded] = useState<boolean>(false);

    useEffect(() => {
        props.getCategoryDetails({
            path: props?.match?.params?.path,
            populate: '*',
        }).then((res: any) => {
            props.getCategoryArticles({
                path: props?.match?.params?.path,
                populate: '*',
                /* pagination: {
                    pageSize: 50,
                    page: 1,
                    withCount: true,
                }, */
                sort: "createdAt:desc"
            }).then((res: any) => {
                setLoaded(true)
            }).catch((err: any) => {
                setLoaded(true)
            });
        }).catch((err: any) => {
            setLoaded(true)
        });
    }, [])

    const renderPost = (item: any) => {

        const goToPost = () => {
            history.push(`/yazi/${item?.path}`)
        }

        return (
            <div className="col-lg-4 justify-content-center d-flex align-items-center">
                <Link to={`/yazi/${item?.attributes?.path}`} style={{
                    color: "black"
                }}>
                    <div className="card blog-card" style={{ margin: "20px 0px" }}>
                        <img
                            src={item?.attributes?.coverImage?.data?.[0]?.attributes?.url || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"}
                            className="card-img-top"
                            width="100%"
                            height="auto"
                            style={{
                                objectFit: "cover",
                                width: "100%",
                                height: "220px"
                            }}
                            alt={item?.attributes?.title} />
                        <div className="card-body blog-body">
                            <h4 className="card-title blog-title" style={{
                                cursor: "pointer"
                            }}>
                                {item?.attributes?.title}
                            </h4>
                            <p className="card-text blog-content">
                                {item?.attributes?.subtitle}
                            </p>
                            <div className="blog-information">
                                <div className="blog-topic">
                                    {item?.attributes?.categories?.data?.[0]?.attributes?.slug}
                                </div>
                                <div className="blog-pub-date">
                                    {item?.attributes?.createdAt && moment(new Date(item?.attributes?.createdAt)).format('LL')}
                                </div>
                            </div>
                        </div>

                    </div>
                </Link >
            </div>
        )
    }

    if (loaded === false) {
        return <PageLoader loaded={false} />
    }

    if (props?.cms?.categoryDetails?.[props?.match?.params?.path] === undefined && loaded === true) {
        history.push("/yazilarim");
        return <PageLoader loaded={false} />
    }

    return (
        <section className="categoryArticle" id="categoryArticle">
            <DocumentSettings docData={{
                title: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.title,
                description: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.description,
                image: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.coverImage?.data?.attributes?.url,
                siteName: `https://www.cangokceaslan.com/yazilarim/${props?.match?.params?.path}`,
                author: "Can Gökçeaslan",
                keywords: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.keywords,
                type: "article",
                publishedTime: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.publishedAt,
                modifiedTime: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.updatedAt,
                section: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.categories?.data?.[0]?.attributes?.path,
                tag: props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.categories?.data?.[0]?.attributes?.slug
            }} />
            {/* <h2 className="blog-header" style={{
                ...props?.hideTitle && {
                    marginTop: -80
                }
            }}>
                {!props?.hideTitle && (
                    <React.Fragment>
                        <img src="assets/svg/medium-sqr.svg" width="40px" height="37px" style={{ marginRight: "12px" }} />
                        <img src="assets/svg/medium-long.svg" width="150px" height="37px" style={{ marginRight: "12px" }} />
                    </React.Fragment>
                )}
            </h2> */}
            {loaded && (
                <>
                    <div className="container">
                        <article className="category-detail-article">
                            <figure className="category-image">
                                <img src={props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.coverImage?.data?.attributes?.url} width="550px" height="auto" alt={props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.coverImage?.data?.attributes?.alternativeText} />
                            </figure>
                            <div className="category-details-texts">
                                <h1 className="category-title">
                                    {props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.title}
                                </h1>
                                <p className="category-description">
                                    <ReactMarkdown children={props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.description}></ReactMarkdown>
                                </p>
                            </div>
                        </article>
                        <hr style={{ marginTop: "40px" }} />
                        <div>
                            <PostSocials />
                        </div>
                        <hr style={{ marginTop: "-40px" }} />
                    </div>
                </>
            )}
            <div className="container-fluid top-container">
                <div className="blog-container">
                    <div className="container">
                        <div className="article-category-details">

                        </div>
                        <div className="row justify-content-center d-flex align-items-center">
                            {(props?.cms?.categoryArticles[props?.match?.params?.path] || []).map((item: any, index: number) => {
                                return renderPost(item)
                            })}
                        </div>
                    </div>
                </div>
            </div>
            <div style={{
                marginBottom: "40px"
            }}></div>
            <SingleCourse courseData={props?.cms?.categoryDetails?.[props?.match?.params?.path]?.attributes?.education?.data?.attributes} />
            {/* <div className="container-fluid top-container" style={{
                ...props?.hideTitle && (
                    sWidth < 992 && {
                        marginTop: "-40px"
                    }
                )
            }}>
                <div className="blog-container">
                    <div className="container">
                        <div className="row justify-content-center d-flex align-items-center">
                            {(props.posts || []).map((item: any, index: number) => renderOnePost(item))}
                            {renderOnePostMedium({})}
                        </div>
                    </div>
                </div>
            </div> */}
        </section>
    );
}

export default connect((state: {
    cms: any
}) => ({
    cms: state.cms,
}), {
    getCategoryDetails,
    getCategoryArticles
})(withRouter(CategoryArticles));